@import '../../variables.scss';
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap');
.commentBox{
	margin-top: 2em;
	ul{
		list-style: none;
		padding: 0;
		.comment{
			display: flex;
			padding: 1em;
			background-color: $color5;
			margin: 1em auto;
			border-radius: 2px;
			li{
				width: 80%;
				font-family: 'Roboto', sans-serif;
				font-size: 20px;
			}
		}
	}
	.votes{
		span{
			padding: 5px;
		    font-size: 20px;
		    cursor: pointer;
		    i{
		    	margin-left: 5px;
		    }
		}
		.like{
	    	color:#44953B;
	 	}
	 	.dislike{
	 		color: #cc2b46;
	 	}
	}
}