@import './variables.scss';

.App{
	display: block;
    width: 80%;
    margin: 1em auto;
    padding: 1em;
    border-radius: 4px;
}
.Input{
	width: 90%;
    padding: 2em;
    border-radius: 9px;
    border: 2px solid $color3;
    &:focus{
    	outline-color:  $color3!important;	
    }
}
.inputBox{
	display: flex;
	button{
		margin-left: 1em;
		height: 3em;
		width: 25%;
		border-radius: 4px;
		border:2px solid $color2;
		margin-top: 1em;
		background-color: transparent;
		color: $color2;
		&:hover{
			font-weight: 600;
		}
	}
}